import React from 'react';
import { CommentPlugin } from '@ali/4ever-bamboo';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export function MobileButton(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper, locale } = props;
  const triggerComment = React.useCallback(() => {
    controller.run('onAction', {
      type: CommentPlugin.actions.ACTION_COMMENT_CLICK,
    });
  }, [controller]);
  const disabled = !controller.query(CommentPlugin.queries.canComment);

  return (
    <Wrapper
      item={item}
      type="line"
      icon="comment"
      lineText={locale?.comment}
      isDisabled={disabled}
      onClick={triggerComment}
    />
  );
}
